<template>
  <div class="wrap">
    <app-header title="经销商列表" showBack="true"></app-header>
    <div class="item-wrap ios-s"
         v-infinite-scroll="loadMore"
         infinite-scroll-disabled="loading"
         infinite-scroll-distance="10"
    >
      <div style="display: flex; margin-bottom: 1rem;">
        <input type="text" v-model="keywords" placeholder="请输入经销商编号或名称" style="flex: 1; padding: .5rem;">
        <mt-button size="small" @click="search" type="primary">搜索</mt-button>
      </div>
      <div class="item"
           v-for="item in userList"
           :key="item.id">
        <div>
          <h5>{{item.name}}</h5>
          <p><span>账号: {{item.code}}</span></p>
        </div>
        <div style="flex: .5">
          <mt-button type="danger" @click="resetPasswordHandel(item.code)" style="font-size: 1rem;">重置密码</mt-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import AppHeader from '../../components/header/header'
  import {getDealerList, resetDealerPassword} from '../../api/maintain'
  export default {
    name: 'Dealer-Account-List',
    data () {
      return {
        userList: [],
        loading: false,
        page: 1,
        keywords: ''
      }
    },
    created () {
      this.getList()
    },
    methods: {
      getList () {
        getDealerList()
          .then(data => {
            if (data.state) {
              this.userList = data.data.list
            } else {
              this.$toast(data.errormsg || '网络错误')
            }
          })
          .catch(err => {
            console.log(err)
            this.$toast('网络错误')
          })
      },
      // 重置密码
      resetPasswordHandel (code) {
        this.$messagebox({
          title: '重置密码',
          message: '确认重置该账号的密码？',
          showCancelButton: true
        })
          .then((data) => {
            if (data === 'confirm') {
              resetDealerPassword(code)
                .then(data => {
                  this.$toast(data.errormsg)
                })
                .catch(err => {
                  console.log(err)
                  this.$toast('网络错误')
                })
            }
          })
      },
      loadMore () {
        this.page = this.page + 1
        this.loading = true
        this.$indicator.open({
          text: '加载中'
        })
        setTimeout(() => {
          getDealerList(this.page)
            .then(data => {
              if (data.state) {
                this.userList.push(...data.data.list)
                this.$indicator.close()
                if (data.pages >= 50) {
                  this.loading = true
                }
                if (data.data.list.length === 0) {
                  this.loading = true
                  return
                }
              } else {
                this.$toast(data.errormsg || '网络错误')
              }
              this.loading = false
            })
            .catch(() => {
              this.$indicator.close()
              this.$toast('网络错误')
            })
        })
      },
      search () {
        getDealerList(null, this.keywords)
          .then(data => {
            if (data.state) {
              this.userList = data.data.list
            } else {
              this.$toast(data.errormsg || '网络繁忙')
            }
          })
          .catch(() => {
            this.$toast('网络错误')
          })
      }
    },
    components: {AppHeader}
  }
</script>

<style lang="less" scoped>
  .wrap {
    height: 100%;
    padding-top: 44px;
    background: #e5e5e5;
    .item-wrap {
      margin-bottom: 100px;
      .item {
        display: flex;
        margin-bottom: 1rem;
        align-items: center;
        padding: .5rem 1rem;
        border-bottom: 1px solid #eee;
        background: #fff;
        box-shadow: 0px 2px 16px 3px rgba(0,0,0, .2);
        div {
          flex: 1;
        }
        h5 {
          margin: 0;
          padding: .2rem 0;
          span {
            font-size: 13px;
          }
        }
        p {
          margin: 0;
          padding: .2rem 0;
          font-size: 13px;
          color: #999;
        }
      }
    }
  }
</style>



